import React, { useState, useEffect } from 'react';
import { useQuery } from 'react-query';
import { Link } from 'react-router-dom';
import { Spinner } from '../../components/Spinner/Spinner';
import type { IEvent } from '../../typings';

import './Main.scss';

const Main = () => {
  const [events, setEvents] = useState([]);

  const query = useQuery('events', async () => {
    console.log('fetch!!!!');
    const response = await fetch(
      'https://cors-anywhere.herokuapp.com/api.smarkets.com/v3/popular/event_ids/sport/football/',
      {
        headers: {
          'Content-Type': 'application/json',
        },
      }
    );
    const result = await response.json();
    const eventIds = result.popular_event_ids;
    const response2 = await fetch(
      `https://cors-anywhere.herokuapp.com/api.smarkets.com/v3/events/${eventIds.join(
        ','
      )}`,
      {
        headers: {
          'Content-Type': 'application/json',
        },
      }
    );
    const result2 = await response2.json();
    // @ts-ignore
    const parsedResult = result2.events.map(({ id, name }) => {
      return { id, name };
    });

    setEvents(parsedResult);
  });
  console.log(events);

  return (
    <div className="Main">
      <h3>Popular events</h3>
      {events.length ? (
        <ul className="EventList">
          {events.map(({ id, name }) => (
            <li key={id} className="Event">
              <Link to={`${id}`}>{name}</Link>
            </li>
          ))}
        </ul>
      ) : (
        <Spinner />
      )}
    </div>
  );
};

export default Main;
